@import "../../assets/sass/variables";

.carousel {
  margin-top: 30%;
  padding-bottom: 20%;
  position: relative;
  box-shadow: 0px 10px 20px rgba(black, 0.25);

  &__icons {
    position: relative;
    display: inline-block;
    width: 100%;
    margin: auto;
  }
  &__icons:after {
    content: normal;
    position: absolute;
    transform: scale(1, 1.8);
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: inline-block;
    background: -moz-linear-gradient(
      top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.65) 100%
    ); /* FF3.6+ */
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      color-stop(0%, rgba(0, 0, 0, 0.65)),
      color-stop(100%, rgba(0, 0, 0, 0))
    ); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(
      top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.65) 100%
    ); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(
      top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.65) 100%
    ); /* Opera 11.10+ */
    background: -ms-linear-gradient(
      top,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.65) 100%
    ); /* IE10+ */
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.65) 100%
    ); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
  }

  &__overlay {
    display: flex;
    justify-content: space-evenly;
    background: none;
    align-items: center;
  }

  &__image {
    display: block;
    height: 200px;
    width: 100vw;
    overflow-y: visible;
    transform: scale(1, 1.7);
    object-fit: cover;
    width: 100%;
  }
  &__arrows {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
  }

  &__arrow {
    display: block;
    height: 30px;

    &:hover {
      cursor: pointer;
    }
  }

  &__text {
    width: 80%;
    display: flex;
    flex-direction: column;
    text-align: center;
    position: absolute;
    z-index: 1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    min-height: 30%;
    margin: auto;
    color: white;
  }
}
@media screen and (min-width: $medium-screen-width) {
  .carousel {
    margin-top: 15%;

    &__image {
      object-fit: fill;
      min-height: 25vh;
    }

    &__arrow {
      display: block;
      height: 30px;

      &:hover {
        cursor: pointer;
      }
    }

    // &__centered {
    //   text-align: left;
    // }

    &__text {
      width: 80%;
      // display: flex;
      // flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }
}

@media screen and (min-width: $laptop-screen-width) {
  .carousel {
    margin-top: 15%;

    &__image {
      object-fit: cover;
      object-position: 90% 10%;
      min-height: 30vh;
    }
  }
}
@media screen and (min-width: $larger-laptop-screen-width) {
  .carousel {
    margin-top: 12%;
    height: 15vh;

    &__image {
      object-fit: cover;
      object-position: 85% 15%;
      height: 10vh;
    }
    &__text {
      font-size: 18px;
    }
  }
}

@media screen and (min-width: $monitor-screen-width){
  .carousel {
    height: 25vh;
    &__image {
      height: 15vh;
    }
    &__text {
      font-size: 36px;
      justify-content: center;
      top: -250px;
  
    }
  }
}

